html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: initial;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  font-size: 40px;
  line-height: 42px;
  text-align: center;
  color: var(--color-light);

  @include small-tablet {
    font-size: 35px;
    line-height: 40px;
  }
  @include mobile {
    font-size: 30px;
    line-height: 35px;
  }
}

.descr {
  font-size: 14px;
  line-height: 20px;
  color: var(--color-silver);

  @include mobile {
    font-size: 12px;
    line-height: 16px;
  }
}

.text {
  font-size: 13px;
  line-height: 20px;
  color: var(--color-light);

  @include mobile {
    font-size: 12px;
    line-height: 16px;
  }
}

.btn {
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.4px;
  color: var(--color-black);
  border-radius: 14px;
  padding: 8px 19px 12px 19px;
  background-color: var(--color-light);
  border: 1px solid var(--color-light);
  transition: background-color 0.3s ease-in-out, border 0.3s ease-in-out;
  &:hover {
    border: 1px solid var(--color-black);
  }
  &:active {
    background-color: var(--color-silver);
  }
  &--secondary {
    color: var(--color-light);
    background-color: var(--color-black);
    border: 1px solid var(--color-black);
    &:hover {
      border: 1px solid var(--color-light);
    }
    &:active {
      background-color: var(--color-black);
    }
  }

  @include mobile {
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.link {
  position: relative;
  display: inline-block;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 0.4px;
  color: var(--color-black);
  border-radius: 14px;
  padding: 8px 9px 12px 41px;
  border: 1px solid var(--color-light);
  background-color: var(--color-light);
  transition: border 0.3s ease-in-out;
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 4px;
    transform: translateY(-50%);
    display: block;
    width: 32px;
    height: 32px;
    background-image: url("../img/link-decorate.svg");
    @include bg-cover;
  }
  &:hover {
    border: 1px solid var(--color-black);
  }

  @include mobile {
    font-size: 14px;
    line-height: 20px;
  }
}

.burger {
  @include burger;
  display: none;

  @include tablet {
    display: flex;
    z-index: 100;
  }
}
